<template>
  <div class="address-group">
    <el-select class="address-input" v-model="proValue" placeholder="全部" :style="seletStyle">
      <el-option v-for="item in province" :key="item.id" :label="item.areaName" :value="item.id" />
    </el-select>
    <el-select class="address-input" v-model="cityValue" placeholder="全部" :style="seletStyle">
      <el-option v-for="item in city" :key="item.id" :label="item.areaName" :value="item.id" />
    </el-select>
    <el-select class="address-input" v-model="areaValue" placeholder="全部" :style="seletStyle">
      <el-option v-for="item in area" :key="item.id" :label="item.areaName" :value="item.id" />
    </el-select>
  </div>
</template>

<script>
// 三级联动
import { areaProvince } from "../data/address";
import queryList from "utils/queryList";
import _ from "lodash";
export default {
  name: "userAddress",
  props:['seletStyle'],
  data() {
    return {
      province: [],
      city: [],
      area: [],
      gradeValue: "",
      proValue: "",
      cityValue: "",
      areaValue: ""
    };
  },
  mounted: function() {
    this.handleProvince();
  },
  methods: {
    handleProvince() {
      //处理省份
      this.province = areaProvince;
    },
    handleData(num, msg) {
      //处理城市和地区
      if( !num ) return;
      queryList('area', {parentId: num}).then((res) => {
        if (res.data.code = '200') {
          if( msg === 'province' ) return this.city = res.data.data;
          this.area = res.data.data;
        } else {
          this.$message('系统繁忙，请稍后再试')
        }
      }).catch((err) => {
        console.log(err)
        this.$message('系统繁忙，请稍后再试!')
      })
    },
    getData(){
      let params = {
        province: this.proValue,
        city: this.cityValue,
        country: this.areaValue
      }
      return params;
    }
  },
  watch: {
    proValue: function(curVal, oldVal) {
      if( !curVal ) return;
      this.city = [];
      this.area = [];
      this.cityValue = "";
      this.areaValue = "";
      this.handleData(curVal,'province');
    },
    cityValue: function(curVal, oldVal) {
      if( !curVal ) return;
      this.area = [];
      this.areaValue = "";
      this.handleData(curVal,'city');
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./../assets/less/color.less");
.address-group {
  display: flex;
  .address-input{
    margin-right: 20px;
    width: 210px;
    &.last-child{
      margin-right: 0;
    }
  }
}
</style>